import React, {useState, useEffect} from 'react';
import axios from './axios';
import "./Row.css"
import YouTube from 'react-youtube';
import movieTrailer from 'movie-trailer';

const base_url = "http://image.tmdb.org/t/p/original/";

function Row({ title, fetchUrl, isLargerRow }) {
    const [movies, setMovie] = useState([]);
    const [trailerUrl, setTrailerUrl] = useState("");

useEffect (() =>{
    async function fetchData(){
        const request = await axios.get(fetchUrl);
        setMovie(request.data.results); 
        return request;
    }
        fetchData();
}, [fetchUrl]); //Run it once and don't repeat again and again
    
    //To link a  yt video
    const opts = {
        hight: "390",
        width: "100%",
        playerVars: {
            //https://developers.google.com/youtube/player_parameters
            autoplay: 1,
        }, 
    };

    const handleClick = (movie) => { 
        if (trailerUrl) {
            setTrailerUrl('');
        } else {
            movieTrailer(movie?.name || "")
                .then((url) => {
                    const urlParams = new URLSearchParams( new URL(url).search);
                    setTrailerUrl(urlParams.get("v"));
                })
                .catch((error) =>  console.log(error));
        }
    }

    return (
        <div className="row">
            <h2>{title}</h2>
            <div className="row_posters">
                {/* Several row posters */}

                {movies.map((movie) => (
                    
                    <img key={movie.id}
                        onClick={() => handleClick(movie)}
                        className={` row_imgPoster ${isLargerRow && "row_posterLarge"}`}
                        src={`${base_url}${isLargerRow ? movie.poster_path : movie.backdrop_path}`}    
                    alt={movie.name}>
                    </img>
                ))}
            </div>
            { trailerUrl && <YouTube videoId={trailerUrl} opts={opts} />}
        </div>
    );
}

export default Row
